import { groups } from "@/api/index";
export default {
    data() {
      return {
        activeName: "",
        join: [],
        creates: [],
        jointle: '',
        createstle: '',

      };
    },  
    methods: {
      gainGroup() {
        groups({}).then( res => {
          console.log(res,46)
           if(res.code == 1) {
              this.join = res.data.join
              this.jointle = res.data.join.length == 0 ? '我加入的群' : '我加入的群(' + res.data.join.length + ')'
              this.createstle  = res.data.create.length == 0 ? '我创建的群' : '我创建的群(' + res.data.create.length + ')'

           }
        })
      }
    },
    created () {
      this.gainGroup()
    },
    components: {}
};



