import { Glisy,Addgroup } from '@/api/index'
import { Dialog } from 'vant' 
export default {
  data () {
    return {
      value: '',
      list: [],
    }
  },
  methods: {
    onSearch() {
      Glisy({pageNo: '1', pageSize: '5', keywords: this.value}).then( res => {
          if(res.code == 1 ) {
            console.log(res)
              this.list = res.data.list
          }
      })
    },
    addGroups(val) {
       Addgroup({groId:val.groId}).then( res => {
          if(res.code == 403 ) {
            console.log(111)
            //  Dialog.alert({
            //     title: '申请入群',
            //     message: '申请成功,请等待群主确认'
            //   }).then(() => {
                 this.onSearch()
              // });

          }
       })
    }
  },
  created () {
    this.onSearch()
  },
  components: {

  }
}



